<template>
  <div>
    <div class="schedule-nav">
      <ul>
        <li :class="{'active': stype === 'keepfit'}" @click="getSchedules('keepfit')">为了腹肌</li>
        <li  :class="{'active': stype === 'stady'}" @click="getSchedules('stady')">哪里不懂点哪里</li>
      </ul>
    </div>
    <div class="clearfix"></div>
    <div class="schedule-list" v-for="(schedule, index) in schedules" :class="{'schedule-today': schedule.is_today}">
      <p>日期：{{ schedule.date }}</p>
      <p>主题：{{ schedule.title }}</p>
      <p>完成情况：{{ schedule.summary }}</p>
      <p>目标：</p>
      <ol>
        <li v-for="(target, t_index) in schedule.targets">
          {{ target.content }}
          <span v-show="target.done" v-if="!schedule.editing">✓</span>
          <input v-show="schedule.editing"  v-if="!target.done" @click="updateTarget(schedule.id, index, t_index, $event)" type="checkbox" name="done" value="完成" />
          <input v-show="schedule.editing"  v-else @click="updateTarget(index, t_index, $event)" type="checkbox" checked="checked" name="done" value="完成" />
        </li>
      </ol>
      <div class="schedule-toolbar">
        <ul>
          <li @click="editSchedule(index)" v-show="!schedule.editing">点击更新</li>
          <li @click="cancleEditSchedule(index)" v-show="schedule.editing">返回</li>
          <li @click="updateSchedule(schedule.id)">删除日程</li>
        </ul>
      </div>
    </div>
   
  </div>
</template>

<script>
import Vue from 'vue';

export default {
  name: 'schedules',
  data () {
    return {
      stype: 'keepfit',
      schedules: []
    }
  },
  created () {
    this.getSchedules(this.stype);
  },
  methods: {
    getSchedules: function (stype) {
      this.stype = stype;
      this.$http.get('/api/schedules/'+stype+'/')
        .then((response) => {
          var data = response.data.data
          for (var i = data.length - 1; i >= 0; i--) {
            data[i].editing = 0
          }
          this.$set(this, 'schedules', data)
        })
        .catch((response) => {
            console.log(response);
            this.$set(this, 'schedules', [])
        })
    },
    editSchedule: function(index) {
      if (this.schedules[index].complete_percent>=100) {
        return false;
      }
      this.schedules[index].editing = 1;
    },
    cancleEditSchedule: function(index) {
      this.schedules[index].editing = 0;
    },
    updateTarget: function(id, index, t_index, e) {
      if (e.target.checked) {
        this.$set(this.schedules[index].targets[t_index], 'done', 1);
      } else {
        this.$set(this.schedules[index].targets[t_index], 'done', 0);
      }
      this.updateSchedule(id, index);
    },
    updateSchedule: function(id, index) {
      var formData = new FormData();
      formData.append('id', id);
      formData.append('targets', JSON.stringify(this.schedules[index].targets));

      this.$http.post('/api/schedule/'+id+'/update/', formData)
        .then((response) => {
          console.log(response);
        })
        .catch((response) => {
          console.log(response);
        })
    },
    delSchedule: function(id, index) {
      return false;
      this.$http.get('/api/schedule/'+id+'/del/')
        .then((response) => {
          console.log(response);
          this.schedules.splice(index, 1);
        })
        .catch((response) => {
          console.log(response);
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.schedule-nav {
  height: 35px;
  background: #ece6e6;
  color: #5d5555;
  border-bottom: 2px solid #ece6e6;
  text-align: center;
}

.schedule-nav ul li {
  float: left;
  width: 50%;
  line-height: 35px;
}

.schedule-nav ul li.active {
  color: #26a2ff;
  font-weight: bold;
  border-bottom: 2px solid #26a2ff;
}


.schedule-list p {
  padding: 0px;
  margin: 0 0 0 15px;
  line-height: 20px
}

.schedule-list ol {
  margin-left: 55px;
}

.schedule-list ol li span {
  color: red;
}

.schedule-today {
  background: #26a2ff;
  color: #ffffff;
}

.schedule-toolbar {
  height: 30px;
  line-height: 30px;background: #ece6e6;
  color: #5d5555;
  text-align: center;
}
.schedule-toolbar ul li {
  float: left;
  width: 50%;
  line-height: 30px;
  font-weight: bold;
}

</style>



