<template>
  <div class="schedule-add">
    <form>
      <div class="form-group">
        <label for="date">日期: </label>
        <input type="text" class="form-control" id="date" placeholder="日期,为空明默认明天" v-model="date" >
      </div>
      <div class="form-group">
        <label for="stype">日程类型: </label>
        <select name="stype"  class="form-control" v-model="stype">
          <option value="keepfit">为了腹肌</option>
          <option value="stady">哪里不懂点哪里</option>
        </select>
      </div>

      <div class="form-group" v-show="stype === 'keepfit' ">
        <label for="title">主题: </label>
        <select name="title"  class="form-control" v-model="title">
          <option v-for="t in titles" :value="t">{{ t }}</option>
        </select>
      </div>
      <div class="form-group" v-show="stype === 'keepfit' ">
        <label for="item1">目标: </label>
        <ol>
          <li v-for="(iname, index) in inames">
            <select :name="iname"  class="form-control" v-model="targets[index]">
              <option v-for="o in options" :value="o">{{ o }}</option>
            </select>
          </li>
        </ol>
      </div>
  
      <div class="form-group" v-show="stype === 'stady' ">
        <label for="title">主题: </label>
        <input type="text" class="form-control" id="title" placeholder="哪里不懂点哪里" v-model="title" >
      </div>
      <div class="form-group" v-show="stype === 'stady' ">
        <label for="item1">目标: </label>
        <ol>
          <li v-for="(iname, index) in inames">
            <input type="text" class="form-control" :name="iname" :id="iname"   v-model="targets[index]">
          </li>
        </ol>
        
      </div>

      <a type="button" class="btn btn-default" style="width: 100%" @click="saveSchedule">提交</a>
    </form>
  </div>
</template>

<script>
var currdate = new Date();
var tomorrow = new Date(new Date().getTime() + 24 * 60 * 60 * 1000);
export default {
  name: 'add',
  data () {
    return {
      options: [
        '',
        'KEEP-徒手胸肌训练',
        '上斜俯卧撑15x4',
        '下斜俯卧撑12x4',
        '哑铃平地卧推15x4',
        '哑铃平地飞鸟12x4',

        'KEEP-腹肌撕裂者进阶',
        '西西里卷腹15x4',
        '仰卧抬腿12x4',
        
        'KEEP-哑铃肩部塑型训练',
        'KEEP-背部塑型',
        '哑铃俯身划船左右12x4',

        'KEEP-拳击燃脂',
        'KEEP-HIIT刷脂 K3',
        'KEEP-HIIT活力 K1',
        'KEEP-HIIT唤醒 K1',
        'KEEP-跳绳HIIT初级版 K2',
        'KEEP-HIIT唤醒K1',
        'KEEP-HIIT燃脂初级 K2',
        '自由跑3KM以上'
      ],
      titles: [
        '虐胸、HIIT刷脂',
        '腹、核心、HIIT刷脂',
        '肩背、HIIT刷脂',
        '全身、跑步',
        '休息日、跑步放松',
        '全身、HIIT刷脂',
      ],
      stype: 'keepfit',
      title: '',
      date: tomorrow.toISOString().slice(0,10),
      targets: new Array(9),
      inames: ['item1', 'item2', 'item3', 'item4', 'item5', 'item6','item7', 'item8', 'item9']
    }
  },
  watch: {
    stype: function(newValue) {
      this.$set(this, 'targets', new Array(9));
      this.$set(this, 'title', '');
    }
  },
  methods: {
    saveSchedule () {
      var formData = new FormData();
      formData.append('stype', this.stype);
      formData.append('startdate', this.date);
      formData.append('title', this.title);
      formData.append('targets', JSON.stringify(this.targets));

      this.$http.post('/api/schedule/add/', formData)
        .then((response) => {
          console.log(response);
          alert('操作成功!');
        })
        .catch((response) => {
          console.log(response);
        })
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.schedule-add {
  padding: 10px;
}

ol {
  margin-left: 10px;
}
</style>



