import Vue from 'vue'
import Router from 'vue-router'
import Schedules from '@/components/Schedules'
import Schedule from '@/components/Schedule'
import Add from '@/components/Add'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
    {
        path: '/',
        redirect: '/schedules/'
    },
    {
        path: '/schedule/add/',
        name: 'Add',
        component: Add
    },
    {
        path: '/schedules/',
        name: 'Schedules',
        component: Schedules
    },
    {
        path: '/schedule/:sid/',
        name: 'Schedule',
        component: Schedule
    }

    ]
})



